import React, { createRef, useEffect } from 'react';
import useMousePointer from '@hooks/useMousePointer';
import styles from './mouse-tracker.module.scss';

const MouseTracker = () => {
  const mouseRef = createRef<HTMLDivElement>();

  const callback = (x, y) => {
    mouseRef.current.style.left = `${x}px`;
    mouseRef.current.style.top = `${y}px`;
  };
  const { setElement } = useMousePointer(callback);

  useEffect(() => {
    if (mouseRef.current !== null) {
      setElement(mouseRef);
    }
  }, [mouseRef]);

  return <div id="mouse-pointer" ref={mouseRef} />;
};

export default MouseTracker;
